
.cartControl {
  font-size: 0;
}
.cartControl .cart-decrease {
  display: inline-block;
  padding: 6px;
}
.cartControl .cart-decrease.move-enter-active {
  transition: all 0.4s linear;
}
.cartControl .cart-decrease .inner {
  display: inline-block;
  line-height: 24px;
  font-size: 24px;
  color: #00a0f0;
  transition: all 0.4s linear;
  transform: rotate(0);
}
.cartControl .cart-decrease.move-enter,
.cartControl .cart-decrease.move-leave-active {
  transition: all 0.4s linear;
  transform: translate3d(24px, 0, 0);
  opacity: 0;
}
.cartControl .cart-decrease.move-enter .inner,
.cartControl .cart-decrease.move-leave-active .inner {
  transform: rotate(180deg);
}
.cartControl .cart-count {
  display: inline-block;
  vertical-align: top;
  text-align: center;
  margin-top: 6px;
  width: 12px;
  line-height: 24px;
  font-size: 10px;
  color: #93999f;
}
.cartControl .cart-add {
  display: inline-block;
  padding: 6px;
  line-height: 24px;
  font-size: 24px;
  color: #00a0f0;
}


